import React from "react";
import { Text, Divider } from "@faharmony/components";
import { useTabs, ITabsProps } from "@faharmony/navigation";
import { Center } from "@faharmony/theme";
import { StyledBody, StyledFooter, StyledView } from "./style";
import { ISummaryViewProps } from "./types";
import { Header } from "./Header";

/**
 * SummaryView
 * ---
 * Summary View is the secondary (temporary) view which can be used to display information.
 *
 * It can be used as base for Modal.
 *
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.7.0
 * @example
 * <SummaryView heading="Heading" caption="type"> {content} </SummaryView>
 */
const SummaryView = (props: ISummaryViewProps) => {
  const { children, footerContent, tabs, caption } = props;

  const defaultContent = children || (
    <Center
      children={<Text value="No content available." color="secondary" />}
    />
  );

  const defaultTab: ITabsProps = {
    id: "default",
    label: "default",
    disabled: true,
    defaultActive: true,
    content: defaultContent,
  };

  const { TabBar, TabPanel } = useTabs(caption, tabs || [defaultTab]);

  return (
    <StyledView className="SummaryView" hasFooter={!!footerContent}>
      {/* Header with Title, Toolbar and Tabs */}
      <Header {...props} TabBar={TabBar} />

      {/* Body */}
      <StyledBody className="SummaryView-body">
        {tabs ? <TabPanel /> : defaultContent}
      </StyledBody>

      {/* Footer */}
      {footerContent ? (
        <StyledFooter>
          <Divider />
          <div className="SummaryView-footer">{footerContent}</div>
        </StyledFooter>
      ) : null}
    </StyledView>
  );
};

export { SummaryView, ISummaryViewProps };
export default SummaryView;
