import React, { useState, useEffect } from "react";
import { Box } from "@faharmony/theme";
import { faForward, faBackward } from "@faharmony/icons";
import { Text, Button, Divider } from "@faharmony/components";
import { TextField } from "../TextField";
import { StyledWrapper, StyledShuttle, StyledItem, className } from "./style";

interface ITransferShuttleItem {
  /** Uniques identifier of each Shuttle Item */
  id: string;
  /** Visible text for users  */
  value: string;
}

interface ITransferShuttleProps {
  /** Visible heading/title of the shuttle */
  heading: string;
  /** List of items available in the shuttle */
  items: ITransferShuttleItem[];
  /** Callback to execute when items are transferred into this shuttle. It receives transferred items as argument. */
  handleAcceptTransfer: (items: ITransferShuttleItem[]) => void;
}

interface IProps {
  shuttle1: ITransferShuttleProps;
  shuttle2: ITransferShuttleProps;
}

const Shuttle = ({
  heading,
  items,
  handleClick,
}: ITransferShuttleProps & {
  handleClick: (items: ITransferShuttleItem[]) => void;
}) => {
  const [filteredItems, setFilteredItems] = useState(items);

  useEffect(() => {
    setFilteredItems(items);
  }, [items]);

  return (
    <StyledShuttle>
      <div className={className + "-heading"}>
        <Text value={heading} variant={"h5"} truncate />
      </div>
      <div className={className + "-filterInput"}>
        <TextField
          onChange={(e) => {
            const val = e.target.value;
            if (val === "") setFilteredItems(items);
            else
              setFilteredItems(() =>
                items.filter(({ value }) =>
                  value.toLowerCase().includes(val.toLowerCase())
                )
              );
          }}
          placeholder="Filter"
        />
      </div>
      <Divider />
      <Box className={className + "-list"} direction="column" spacing="sm">
        {filteredItems.length ? (
          filteredItems.map((item, index) => (
            <StyledItem
              key={item.id}
              index={index}
              id={className + "-Item-" + item.id}
              onClick={() => handleClick([item])}
            >
              <Text value={item.value} truncate />
            </StyledItem>
          ))
        ) : (
          <Text value="No items found" variant="small" color="disabled" />
        )}
      </Box>
    </StyledShuttle>
  );
};

/**
 * TransferShuttle
 * ---
 * A transfer shuttle enables the user to move one or more shuttle items between shuttles.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.5.0
 * @example
 * <TransferShuttle shuttle1={} shuttle2={}>
 */
const TransferShuttle = React.forwardRef<HTMLDivElement, IProps>(
  ({ shuttle1, shuttle2 }, ref) => (
    <StyledWrapper className="transferShuttle" ref={ref}>
      <Shuttle {...shuttle1} handleClick={shuttle2.handleAcceptTransfer} />
      <Shuttle {...shuttle2} handleClick={shuttle1.handleAcceptTransfer} />
      <Box id={className + "-buttons"} direction="column">
        <Button
          icon={faForward}
          variant="secondary"
          spacing="sm"
          tooltip={`Move all items to ${shuttle2.heading}`}
          disabled={shuttle1.items.length === 0}
          onClick={() => shuttle2.handleAcceptTransfer(shuttle1.items)}
        />
        <Button
          icon={faBackward}
          variant="secondary"
          spacing="sm"
          tooltip={`Move all items to ${shuttle1.heading}`}
          disabled={shuttle2.items.length === 0}
          onClick={() => shuttle1.handleAcceptTransfer(shuttle2.items)}
        />
      </Box>
    </StyledWrapper>
  )
);

export { TransferShuttle, ITransferShuttleProps, ITransferShuttleItem };
