import { OnClickEventType } from "./typeDefinitions";

export const NOOP = () => null;

export const preventDefaultOnClick = (e: OnClickEventType) => {
  e?.preventDefault();
  e?.stopPropagation();
};

export const padStart = (num: number, pad = 2): string =>
  num.toString().padStart(pad, "0");

export const padEnd = (num: number, pad = 2): string =>
  num.toString().padEnd(pad, "0");

/**
 * Returns a valid HTML tag which can be used for building components.
 * @param tagName
 */
export const getValidTagName = (tagName?: keyof HTMLElementTagNameMap) => {
  let Tag = "div";
  if (
    tagName !== undefined &&
    tagName !== "applet" &&
    tagName !== "basefont" &&
    tagName !== "dir" &&
    tagName !== "font" &&
    tagName !== "frame" &&
    tagName !== "frameset" &&
    tagName !== "marquee" &&
    tagName !== "slot"
  ) {
    Tag = tagName;
  }
  return Tag;
};

/** Create range of numbers */
export const range = (from: number, to: number, step: number = 1) =>
  Array.from({ length: (to - from) / step + 1 }, (_, i) => from + i * step);

/** Merge multiple refs */
export const mergeRefs = <T = any>(
  refs: Array<React.MutableRefObject<T> | React.LegacyRef<T>>
): React.RefCallback<T> => (value) => {
  refs.forEach((ref) => {
    if (typeof ref === "function") ref(value);
    else if (ref != null)
      (ref as React.MutableRefObject<T | null>).current = value;
  });
};
