import React from "react";
import { faServicesApiInstance } from "@faharmony/service";
import { FAHarmonySVGPath } from "@faharmony/icons";
import { IFABarAppButtonProps } from "./types";

/** Dummy app for devMode */
const harmonyApp: IFABarAppButtonProps = {
  position: 0,
  id: "fa-harmony",
  appName: "FA Harmony",
  iconPath: FAHarmonySVGPath,
  url: "/",
  openInNewWindow: false,
};

/** Fetch appList from navigator micro-service */
export default (devMode: boolean = false): IFABarAppButtonProps[] => {
  const [apps, setApps] = React.useState<IFABarAppButtonProps[]>([]);
  React.useEffect(() => {
    devMode
      ? // Set dummy app in devMode
        setApps([harmonyApp])
      : // Fetch if not in devMode
        faServicesApiInstance
          .get("navigator/")
          .then(({ data }) => data && Array.isArray(data) && setApps(data));
  }, [devMode]);

  return apps.sort(({ position: a }, { position: b }) => a - b);
};
