import React from "react";
import { Text, Icon, Tooltip } from "@faharmony/components";
import { uuid } from "@faharmony/helpers";
import { faQuestionCircle } from "@faharmony/icons";
import { Theme, Box } from "@faharmony/theme";

interface IFormControlProps {
  children: React.ReactChild;
  label?: string;
  helpText?: string;
  errorText?: string;
  placement?: "top" | "left";
  required?: boolean;
}

/**
 * FormControl
 * ---
 * Wrapper around other form components tp provide label, help-text, errors, etc.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @copyright FA Solutions Oy
 * @jira https://fasolutions.jira.com/browse/HAR-48
 * @version 1.6.0
 * @example <FormControl><TextField /></FormControl>
 */
const FormControl = (props: IFormControlProps) => {
  const {
    label,
    helpText,
    errorText,
    placement = "column",
    required,
    children,
  } = props;
  return (
    <Box
      id={"FormControl-" + label || uuid()}
      spacing="xs"
      alignItems="start"
      direction={placement === "left" ? "row" : "column"}
    >
      {(label || helpText) && (
        <Box justifyContent="space-between">
          <Box justifyContent="flex-start" spacing="sm">
            <div>
              <Text value={label || ""} variant="h6" />
            </div>
            {required && (
              <div style={{ color: Theme.colorTint(), cursor: "default" }}>
                <Tooltip value="Required">
                  <Text value={"*"} variant="h6" />
                </Tooltip>
              </div>
            )}
          </Box>
          {helpText && (
            <Box justifyContent="flex-end">
              <Tooltip value={helpText}>
                <span style={{ fontSize: "0.8rem", cursor: "help" }}>
                  <Icon
                    icon={faQuestionCircle}
                    size="1x"
                    color={Theme.colorText("secondary")}
                  />
                </span>
              </Tooltip>
            </Box>
          )}
        </Box>
      )}
      {children}
      {errorText && (
        <span
          style={{
            color: Theme.colorTint("danger"),
          }}
        >
          <Text value={errorText || helpText} variant="small" />
        </span>
      )}
    </Box>
  );
};

export { FormControl, IFormControlProps };
