import { useModule } from "@faharmony/module";
import { useSelector } from "react-redux";
import { createSlice } from "@reduxjs/toolkit";

/** This function wraps RTK createSlice() and expects ModuleID as name of slice-state.
 * @see [Writing the Slice Reducer](https://redux-toolkit.js.org/usage/usage-with-typescript#createslice) */
const createModuleState = createSlice;

/** Hook to access state of current Module using `useSelector`.
 *  State tye definition must be provided explicitly. */
const useModuleState = <S extends any = {}>() => {
  return useSelector<any, S>((state) => state[useModule().id]);
};

export { StateProvider } from "./Provider";
export { createModuleState, useModuleState };

/** @external */
export * from "@reduxjs/toolkit";
export {
  useStore,
  useDispatch,
  useSelector,
  connect,
  DispatchProp,
  Selector,
  Connect,
  ConnectOptions,
  DefaultRootState,
  shallowEqual,
  createDispatchHook,
  createStoreHook,
  createSelectorHook,
} from "react-redux";
