import React from "react";
import InputNumber from "rc-input-number";
import { InputField, IInputFieldProps } from "../BaseInputField";
import { useFormContext, Controller, ControllerProps } from "../hook-form";

type OnChangeParam = string | number | undefined;
interface INumberFieldProps extends Omit<IInputFieldProps, "onChange"> {
  /** Default value of uncontrolled Field */
  defaultValue?: number;
  /** Value of controlled Field */
  value?: number;
  /** Get current value of Field */
  onChange?: (value: OnChangeParam) => void;
  /** Character used for decimal separator. @default '.' */
  decimalSeparator?: "." | ",";
  /** Number of digits after decimal. @default 2 */
  precision?: number;
  /** Maximum value of Field. @default MAX_SAFE_INTEGER */
  max?: number;
  /** Minimum value of Field. @default -MAX_SAFE_INTEGER */
  min?: number;

  // formatter?: (value: OnChangeParam) => string;
  // parser?: (displayValue: string | undefined) => number | string;
  // Control
  rules?: ControllerProps<any>["rules"];
  render?: ControllerProps<any>["render"];
  onFocus?: (...arg: any[]) => void;
}

/** InputField for numbers */
const Field = (props: INumberFieldProps) => {
  const {
    spacing,
    prefixIcon,
    prefixText,
    suffixIcon,
    suffixText,
    size,
    isClearable,
    onInputClear,
    error,
    success,
    isLoading,
    render,
    name,
    rules,
    onFocus,
    ...others
  } = props;
  const { onChange, defaultValue, ...inputProps } = others;

  const [currentValue, setValue] = React.useState<string | number>(
    defaultValue || ""
  );
  const handleChange = (val: OnChangeParam) => {
    onChange && onChange(val);
    setValue(val?.toString() || "");
  };

  // Error if not number or decimal
  const isRegexError = /[^0-9.]/g.test(currentValue.toString());
  const isError = error || isRegexError;

  const ifProps: Omit<INumberFieldProps, "onChange"> = React.useMemo(
    () => ({
      spacing,
      prefixIcon,
      prefixText,
      size,
      isClearable,
      onInputClear,
      error: isError,
      success,
      isLoading,
      suffixIcon,
      suffixText,
      textAlign: "right",
    }),
    [isError]
  );

  return (
    <InputField
      {...ifProps}
      name={name}
      ref={null}
      inputComponent={
        <InputNumber
          name={name}
          useTouch={true}
          defaultValue={defaultValue}
          decimalSeparator="."
          precision={2}
          {...inputProps}
          ref={null}
          onChange={handleChange}
          upHandler={undefined}
          downHandler={undefined}
        />
      }
    />
  );
};

/**
 * NumberField
 * ---
 * Component to manage/format input for numeric value
 * @jira https://fasolutions.jira.com/browse/HAR-76
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @copyright FA Solutions Oy
 * @version 1.6.0
 * @example <NumberField defaultValue={999.99} />
 */
const NumberField = ({
  rules,
  render,
  onFocus,
  name,
  ...other
}: INumberFieldProps) => {
  try {
    const { control } = useFormContext();
    return (
      <Controller
        as={(<Field {...other} />) as any}
        control={control}
        name={name || "number"}
        rules={rules}
        render={render}
        onFocus={onFocus}
      />
    );
  } catch {
    return <Field {...other} />;
  }
};

export { NumberField, INumberFieldProps };
