import React from "react";
import { faTimesCircle } from "@faharmony/icons";
import {
  Text,
  Toolbar,
  Divider,
  ButtonPopover,
  IIconButtonPopoverObject,
} from "@faharmony/components";
import { Theme, Grid, Box } from "@faharmony/theme";
import { StyledHead } from "./style";
import { ISummaryViewProps } from "./types";

const getHeaderActions = ({
  actions = [],
  actionsInHeader,
  onClose,
}: ISummaryViewProps) => {
  let headerActions: IIconButtonPopoverObject[] = [];
  if (actionsInHeader)
    headerActions = [...actions] as IIconButtonPopoverObject[];
  const closeButtonObject: IIconButtonPopoverObject = {
    id: "close-summary-view",
    onClick: onClose,
    icon: faTimesCircle,
    tooltip: "Close",
  };
  return [...headerActions, closeButtonObject];
};

const Title = (
  props: ISummaryViewProps & { headerActions: IIconButtonPopoverObject[] }
): JSX.Element => {
  const { caption, heading, headerActions, headingSuffix } = props;
  const hasSuffix = !!headingSuffix;
  return (
    <Grid gridTemplate="auto / auto max-content" overflow="inherit">
      <div className="SummaryView-header-text">
        <Text
          value={caption.toString()}
          variant="strong"
          color="secondary"
          truncate
        />
        <Box>
          <Text value={heading} variant="h3" truncate={!hasSuffix} />
          {headingSuffix}
        </Box>
      </div>
      <Box
        className={"SummaryView-header-actions"}
        height="100%"
        alignItems="unset"
        style={{ paddingTop: Theme.spacing() }}
      >
        {headerActions.map((action) => (
          <ButtonPopover key={action.id} {...action} spacing="sm" outline />
        ))}
      </Box>
    </Grid>
  );
};

export const Header = (
  props: ISummaryViewProps & { TabBar: () => JSX.Element }
): JSX.Element => {
  const { actionsInHeader, actions = [], toolbarContent, tabs, TabBar } = props;

  return (
    <StyledHead className="SummaryView-header">
      <Title {...props} headerActions={getHeaderActions(props)} />
      {toolbarContent ? (
        <Toolbar children={toolbarContent} />
      ) : (
        !!actions.length && !actionsInHeader && <Toolbar actions={actions} />
      )}
      {tabs ? <TabBar /> : <Divider />}
    </StyledHead>
  );
};
