import React from "react";
import { Icon, Text } from "@faharmony/components";
import { Box } from "@faharmony/theme";
import { faCircle } from "@faharmony/icons";
import { faDotCircle } from "@faharmony/icons/solid";
import { StyledInputLabel, HTMLInputCommonType } from "../common";

type RadioOptionType = {
  value: string;
  label?: string;
  isDisabled?: boolean;
};

interface IRadioGroupProps
  extends Pick<
    React.DetailedHTMLProps<
      React.InputHTMLAttributes<HTMLInputElement>,
      HTMLInputElement
    >,
    HTMLInputCommonType
  > {
  name: string;
  defaultValue?: string;
  options: RadioOptionType[];
  direction?: "row" | "column";
}

/**
 * RadioGroup
 * ---
 * Group of radio components
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @copyright FA Solutions Oy
 * @jira https://fasolutions.jira.com/browse/HAR-48
 * @version 1.6.0
 * @example <RadioGroup name="radio" options={[{label: "Option1", value: "o1"}]} />
 */
const RadioGroup = React.forwardRef<HTMLInputElement, IRadioGroupProps>(
  (
    {
      options = [],
      direction = "row",
      disabled: allDisabled,
      defaultValue,
      ...inputProps
    },
    ref
  ) => (
    <Box
      id={"RadioGroup-" + inputProps.name}
      direction={direction}
      alignItems="start"
      tabIndex={0}
    >
      {options.map((o) => (
        <StyledInputLabel
          key={o.value}
          className="Radio"
          disabled={allDisabled || o.isDisabled}
        >
          <input
            type="radio"
            ref={ref}
            value={o.value}
            disabled={allDisabled || o.isDisabled}
            defaultChecked={defaultValue === o.value}
            {...inputProps}
          />
          {
            <React.Fragment>
              <Icon icon={faDotCircle} className="checked" />
              <Icon icon={faCircle} className="unchecked" />
            </React.Fragment>
          }
          {o.label && <Text>{o.label}</Text>}
        </StyledInputLabel>
      ))}
    </Box>
  )
);

export { RadioGroup, IRadioGroupProps, RadioOptionType };
