import { styled, theme } from "@faharmony/theme";

const defaultTheme = {
  width: "40px",
  height: "20px",

  borderWidth: "1px",
  borderColor: theme.color.outline.border,
  leftBorderColor: theme.color.outline.border,
  rightBorderColor: theme.color.outline.border,

  leftBackgroundColor: theme.color.text.disabled,
  rightBackgroundColor: theme.color.background.inverse,
  backgroundColorDisabled: theme.color.background.disabled,

  radius: "256px",
  radiusBackground: "256px",
  knobRadius: "100%",
  // radius: "4px",
  // radiusBackground: "3px",
  // knobRadius: "2px",

  knobWidth: "16px",
  knobHeight: "16px",
  knobGap: "2px",

  knobColor: theme.color.background.elevated,
  knobColorDisabled: theme.color.text.disabled,
  leftKnobColor: theme.color.background.base,
  rightKnobColor: theme.color.background.elevated,
};

export const StyledToggle = styled.div`
  position: relative;
  box-sizing: border-box;
  display: inline-grid;
  align-items: center;
  width: ${defaultTheme.width};
  height: ${defaultTheme.height};
  vertical-align: middle;
  margin: 0 4px;

  input[type="checkbox"] {
    position: absolute;
    margin-left: -9999px;
    visibility: hidden;
    /* off state */
    & + label {
      display: inline-grid;
      box-sizing: border-box;
      align-items: center;
      outline: none;
      user-select: none;
      width: ${defaultTheme.width};
      height: ${defaultTheme.height};
      background-color: ${defaultTheme.leftBorderColor};
      border-radius: ${defaultTheme.radius};
      cursor: pointer;
      transition: background ease-out 0.2s;
      &:before {
        content: "";
        display: block;
        position: absolute;
        border-radius: ${defaultTheme.radiusBackground};
        width: calc(${defaultTheme.width} - 2 * ${defaultTheme.borderWidth});
        height: calc(${defaultTheme.height} - 2 * ${defaultTheme.borderWidth});
        background-color: ${defaultTheme.leftBackgroundColor};
        left: ${defaultTheme.borderWidth};
      }
      &:after {
        display: block;
        position: absolute;
        content: "";
        width: ${defaultTheme.knobWidth};
        height: ${defaultTheme.knobHeight};
        border-radius: ${defaultTheme.knobRadius};
        background-color: ${defaultTheme.leftKnobColor};
        transition: all ease-out 0.2s;
        margin-left: ${defaultTheme.knobGap};
        box-shadow: ${theme.shadow.default};
      }
    }
    /* disabled */
    &:disabled {
      & + label {
        background-color: ${defaultTheme.borderColor};
        cursor: default;
        &:before {
          background-color: ${defaultTheme.backgroundColorDisabled};
        }
        &:after {
          box-shadow: none;
          background-color: ${defaultTheme.knobColorDisabled};
        }
      }
    }
    /* on state */
    &:checked {
      & + label {
        background-color: ${defaultTheme.rightBorderColor};
        &:before {
          background-color: ${defaultTheme.rightBackgroundColor};
        }
        &:after {
          margin-left: calc(
            100% - ${defaultTheme.knobWidth} - ${defaultTheme.knobGap}
          );
          transition: all ease-out 0.2s;
          background-color: ${defaultTheme.rightKnobColor};
        }
      }
      &:disabled {
        & + label {
          background-color: ${defaultTheme.borderColor};
          &:before {
            background-color: ${defaultTheme.backgroundColorDisabled};
          }
          &:after {
            box-shadow: none;
            background-color: ${defaultTheme.knobColorDisabled};
          }
        }
      }
    }
  }
`;
