// Defaults
const className = "DatePicker";
const defaultDateFormat = "yyyy-M-d";
const defaultDisabledDates = [{ daysOfWeek: [0, 6] }];

export interface IPickerProps {
  disabled?: boolean;
  // DayPicker
  format?: DayPickerInputProps["format"];
  disabledDates?: DayPickerProps["disabledDays"];
  placeholder?: string;
  value?: Date;
}

const CalendarOverlay = React.forwardRef(
  ({ children, classNames, ...props }: any, ref) => (
    <StyledCalendar {...props} className={classNames.overlayWrapper} ref={ref}>
      {children}
    </StyledCalendar>
  )
);

const NavBar = ({
  onPreviousClick,
  onNextClick,
  month,
  localeUtils,
  previousMonth,
  nextMonth,
  showNextButton,
  showPreviousButton,
}: NavbarElementProps) => {
  const currentMonth = localeUtils.getMonths()[month.getMonth()];
  const currentYear = month.getFullYear().toString();
  const months = localeUtils.getMonths();
  const prev = months[previousMonth.getMonth()];
  const next = months[nextMonth.getMonth()];
  return (
    <Box justifyContent="space-between" className="Calendar-Navigation">
      {showPreviousButton && (
        <Button
          icon={faAngleLeft}
          onClick={() => onPreviousClick()}
          spacing="xs"
          tooltip={prev}
        />
      )}
      <Text
        value={currentMonth + " " + currentYear}
        variant="h5"
        align="center"
      />
      {showNextButton && (
        <Button
          icon={faAngleRight}
          onClick={() => onNextClick()}
          spacing="xs"
          tooltip={next}
        />
      )}
    </Box>
  );
};

