import { Trigger } from "@faharmony/helpers";
import { IToastProps } from "./types";

// triggers
export const addToastTrigger = new Trigger<IToastProps>();
export const removeToastTrigger = new Trigger<string>();
export const clearToastTrigger = new Trigger();

/** Add a new toast to the toast-notification stack
 * @example addToast({id: "uid", title: "Notification" }) */
export const addToast = addToastTrigger.publish;
/** Remove a ID-specific toast from the toast-notification stack
 * @example removeToast("uid") */
export const removeToast = removeToastTrigger.publish;
/** Clear the toast-notification stack
 * @example clearToasts */
export const clearToasts = () => clearToastTrigger.publish({});
