import { styled, theme, getTextDimensions, SMLType } from "@faharmony/theme";

import { _IInputFieldContainerProps } from "./types";
import { inputHeight, calcHeight } from "../common";

const commonClass = "InputField";
const transitionTime = "0.3s";

const calcTextWidth = (text: string | undefined, spacing: SMLType = "md") => {
  if (!text) return 0;
  const height = calcHeight({ spacing });
  const width = getTextDimensions(text, "p").width + 10;
  if (width < height) return height;
  else return width;
};

const calcPrefixWidth = ({
  prefixIcon,
  prefixText,
  spacing,
}: _IInputFieldContainerProps) =>
  prefixIcon ? calcHeight({ spacing }) : calcTextWidth(prefixText, spacing);

const calcSuffixWidth = ({
  suffixIcon,
  suffixText,
  spacing,
}: _IInputFieldContainerProps) =>
  suffixIcon ? calcHeight({ spacing }) : calcTextWidth(suffixText, spacing);

const getOutlineColor = (
  { error, success }: _IInputFieldContainerProps,
  state: string = ""
) =>
  error
    ? theme.color.tint.red
    : success
    ? theme.color.tint.green
    : state === "focus"
    ? theme.color.outline.borderHover
    : theme.color.outline.border;

const getTextColor = (
  { error, success }: _IInputFieldContainerProps,
  state: string = ""
) =>
  error
    ? theme.color.tint.red
    : success
    ? theme.color.tint.green
    : state === "focus"
    ? theme.color.text.primary
    : state === "disabled"
    ? theme.color.text.disabled
    : theme.color.text.secondary;

const getBackgroundColor = (
  { disabled, readonly }: _IInputFieldContainerProps,
  variant: string = ""
) =>
  disabled
    ? theme.color.background.disabled
    : readonly || variant === "prefix"
    ? theme.color.background.base
    : theme.color.background.input;

const StyledInputField = styled.div<_IInputFieldContainerProps>`
  position: relative;
  width: 100%;
  height: auto;

  input {
    min-width: 20px;
    width: 100%;
    height: ${calcHeight}px;
    border-radius: ${theme.radius.sm};
    border: 1px solid ${getOutlineColor};
    overflow: hidden;
    box-shadow: ${theme.shadow.inner};
    background-color: ${getBackgroundColor};
    padding-left: ${(p) => calcPrefixWidth(p) + 4}px;
    padding-right: ${(p) => calcSuffixWidth(p) + 4}px;
    color: ${getTextColor};
    transition: border ${transitionTime};
    text-align: ${(p) => p.textAlign};

    :hover,
    :focus {
      color: ${(p) => getTextColor(p, "focus")};
      border-color: ${(p) =>
        p.disabled ? "none" : getOutlineColor(p, "focus")};
    }

    :read-only {
      color: ${getTextColor};
    }

    :disabled {
      color: ${(p) => getTextColor(p, "disabled")};
    }

    ::placeholder {
      color: ${theme.color.text.disabled};
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  .prefix,
  .suffix {
    position: absolute;
    top: 1px;
    bottom: 0;
    max-width: 100px;
    min-width: 20px;
    text-align: center;
    padding: 0 ${(p) => (p.prefixText ? theme.spacing.sm : 0)};
    padding-top: ${(p) =>
      (calcHeight(p) - inputHeight - (p.prefixIcon ? 2 : 0)) / 2}px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: ${theme.color.text.disabled};
    background-color: transparent;
  }

  .prefix {
    left: 0;
    width: ${calcPrefixWidth}px;
    ::after {
      right: 0;
    }
  }

  .suffix {
    right: 0;
    width: ${calcSuffixWidth}px;
    ::before {
      left: 0;
    }
  }

  .prefix::after,
  .suffix::before {
    content: "";
    position: absolute;
    top: ${theme.spacing.sm};
    bottom: ${theme.spacing.sm};
    width: 1px;
    background-color: ${theme.color.outline.border};
  }

  .clearButton {
    position: absolute;
    right: ${theme.spacing.sm};
    top: 50%;
    transform: translateY(-50%);
    color: ${theme.color.text.disabled};
    cursor: pointer;
    transition: color ${transitionTime};
    :hover {
      color: ${theme.color.text.primary};
    }
  }
`;

export { commonClass, calcPrefixWidth, StyledInputField };
