import React from "react";
import { styled } from "@faharmony/theme";

interface Props {
  color?: string;
  size?: string;
}

const defColor = "grey";

const Styled = styled.div<Props>`
  display: inline-block;
  position: relative;
  width: ${(p) => p.size || "1rem"};
  height: ${(p) => p.size || "1rem"};

  div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    border: 2px solid ${(p) => p.color || defColor};
    border-radius: 50%;
    animation: rcs-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: ${(p) => p.color || defColor} transparent transparent
      transparent;
    :nth-of-type(1) {
      animation-delay: -0.45s;
    }
    :nth-of-type(2) {
      animation-delay: -0.3s;
    }
    :nth-of-type(3) {
      animation-delay: -0.15s;
    }
  }
  @keyframes rcs-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
`;

export const RingLoader = (props: Props) => (
  <Styled className={"RingLoader"} {...props}>
    <div />
    <div />
    <div />
    <div />
  </Styled>
);
