import React from "react";
import { InputField, IInputFieldProps } from "../BaseInputField";

interface ITextFieldProps extends IInputFieldProps {
  /** Allowed HTML input types for this field */
  type?: "text" | "email" | "password" | "tel" | "url";
}

/**
 * TextField
 * ---
 * Component to manage single line input for text
 * @jira https://fasolutions.jira.com/browse/HAR-ticker
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @copyright FA Solutions Oy
 * @version 1.6.0
 * @example <TextField />
 */
const TextField = React.forwardRef<HTMLInputElement, ITextFieldProps>(
  (props, ref) => {
    return <InputField {...props} ref={ref} type={props.type || "text"} />;
  }
);

export { TextField, ITextFieldProps };
