import React from "react";
import { uuid, usePropState } from "@faharmony/helpers";
import { Icon, Text } from "@faharmony/components";
import { faSquare } from "@faharmony/icons";
import { faMinusSquare, faCheckSquare } from "@faharmony/icons/solid";
import {
  InputPropError,
  HTMLInputCommonType,
  StyledInputLabel,
} from "../common";

type OnInputChangeType = React.ChangeEvent<HTMLInputElement>;
type HTMLInputCheckboxType = HTMLInputCommonType | "defaultChecked" | "checked";

interface ICheckboxProps
  extends Pick<
    React.DetailedHTMLProps<
      React.InputHTMLAttributes<HTMLInputElement>,
      HTMLInputElement
    >,
    HTMLInputCheckboxType
  > {
  indeterminate?: boolean;
  label?: string;
}

/**
 * Checkbox
 * ---
 * Simple checkbox component
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @copyright FA Solutions Oy
 * @jira https://fasolutions.jira.com/browse/HAR-48
 * @version 1.6.0
 * @example <Checkbox onChange={console.log} />
 */
const Checkbox = React.forwardRef<HTMLInputElement, ICheckboxProps>(
  (props, ref) => {
    const {
      indeterminate,
      label,
      defaultChecked,
      checked,
      onChange,
      name,
      ...otherProps
    } = props;
    const [isChecked, setChecked] = usePropState(checked);

    // Return
    if (checked !== undefined && defaultChecked !== undefined)
      return <InputPropError value="checked" />;

    const ID = name || label || uuid();
    const handleChange = (event: OnInputChangeType) => {
      event?.stopPropagation();
      setChecked(event?.target.checked);
      onChange && onChange(event);
    };
    const inputProps: ICheckboxProps = React.useMemo(
      () =>
        checked !== undefined
          ? { checked: isChecked, onChange: handleChange }
          : { defaultChecked, onChange },
      [isChecked, handleChange, defaultChecked, onChange]
    );
    return (
      <StyledInputLabel
        className="CheckboxField"
        id={ID}
        disabled={props.disabled}
      >
        <input
          type="checkbox"
          ref={ref}
          name={ID}
          {...inputProps}
          {...otherProps}
        />
        {indeterminate ? (
          <Icon icon={faMinusSquare} className="indeterminate" />
        ) : (
          <React.Fragment>
            <Icon icon={faCheckSquare} className="checked" />
            <Icon icon={faSquare} className="unchecked" />
          </React.Fragment>
        )}
        {label && <Text>{label}</Text>}
      </StyledInputLabel>
    );
  }
);

export { Checkbox, ICheckboxProps };
