import React from "react";
import ReactSelect, { Props } from "react-select";
import ReactSelectAsync from "react-select/async";

import { useFormContext, Controller } from "../hook-form";
import { allComponents as components } from "./components";
import { selectStyles, selectTheme } from "./style";
import {
  ComboboxOptionType,
  IComboboxProps,
  ControllerType,
  ComboboxOptionsType,
} from "./types";

const Selector = (props: IComboboxProps) => {
  const {
    onFocus,
    isLoading,
    isDisabled,
    options,
    loadOptions,
    defaultOptions,
    cacheOptions,
    ...selectProps
  } = props;

  const postSelectOptions: Props = {
    className: "Combobox",
    styles: selectStyles,
    closeMenuOnSelect: !props.isMulti,
    hideSelectedOptions: false,
    components,
    theme: (t) => selectTheme(t, props.spacing),
    options: loadOptions ? undefined : options,
  };
  const preSelectOptions: Props = {
    isClearable: true,
    isSearchable: true,
    isLoading,
    isDisabled: isDisabled || isLoading,
  };

  const commonProps = {
    ...preSelectOptions,
    ...selectProps,
    ...postSelectOptions,
  };

  return loadOptions ? (
    <ReactSelectAsync
      {...commonProps}
      loadOptions={loadOptions}
      defaultOptions={defaultOptions}
      cacheOptions={cacheOptions}
    />
  ) : (
    <ReactSelect {...commonProps} />
  );
};

/**
 * Combobox
 * ---
 * Component to search and select options.
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @copyright FA Solutions Oy
 * @jira https://fasolutions.jira.com/browse/HAR-69
 * @version 1.6.0
 * @example <Combobox options={[{label: "Opt1", value: 'op1'}]} />
 */
const Combobox = (props: IComboboxProps & ControllerType) => {
  const { name, rules, render, onFocus } = props;
  const reactSelect = (<Selector {...props} />) as any;
  // Check if component is placed in a react-hook-form.
  try {
    const { control } = useFormContext();
    return (
      <Controller
        as={reactSelect}
        control={control}
        rules={rules}
        render={render}
        name={name || "combobox"}
        onFocus={onFocus}
        defaultValue={props.defaultValue}
      />
    );
  } catch {
    return reactSelect;
  }
};

export { Combobox, IComboboxProps, ComboboxOptionType, ComboboxOptionsType };
