import React from "react";
import { useResponsive } from "@faharmony/helpers";
import { Center } from "@faharmony/theme";
import { Text, Divider } from "@faharmony/components";
import { useTabs, ITabsProps } from "@faharmony/navigation";
import { Div100vh } from "../layouts/Div100vh";
import { IPageViewProps } from "./types";
import { StyledFooter, StyledView, StyledBody } from "./style";
import { Header } from "./Header";

/**
 * PageView
 * ---
 * Page View is the primary view which is used as base for all pages in FA App.
 *
 * It should NOT be used as base for Modals. See SummaryView/ContentView for that.
 *
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.7.0
 * @example
 * <PageView headerTitle="Heading"> {content} </PageView>
 */
const PageView = (props: IPageViewProps) => {
  const { footerContent, overflow = "hidden", heading, tabs, children } = props;
  const { isMobile } = useResponsive();

  const defaultContent = children || (
    <Center
      children={<Text value="No content available." color="secondary" />}
    />
  );

  const defaultTab: ITabsProps = {
    id: "default",
    label: "default",
    disabled: true,
    defaultActive: true,
    content: defaultContent,
  };

  const { TabBar, TabPanel } = useTabs(heading, props.tabs || [defaultTab]);

  return (
    <Div100vh style={{ height: isMobile ? "calc(100rvh - 48px)" : "100rvh" }}>
      <StyledView className="pageView" hasFooter={!!footerContent}>
        {/* Header */}
        <Header {...props} TabBar={TabBar} />

        {/* Body */}
        <StyledBody overflow={overflow} className="PageView-body">
          {tabs ? <TabPanel /> : defaultContent}
        </StyledBody>

        {/* Footer */}
        {footerContent ? (
          <StyledFooter>
            <Divider />
            <div className="PageView-footer">{footerContent}</div>
          </StyledFooter>
        ) : null}
      </StyledView>
    </Div100vh>
  );
};

export { PageView, IPageViewProps };
