import React from "react";
import { HTMLPropsType } from "@faharmony/helpers";
import { Text } from "@faharmony/components";
import { Center } from "@faharmony/theme";
import { TabsBar } from "./TabBar";
import { ITabsProps, TabHelperType, UseTabsReturnProps } from "./types";
import { tabIdTrigger } from "./helpers";

/**
 * useTabs
 * ---
 * Tabbed navigation used in PageView and SummaryView
 * @jira https://fasolutions.jira.com/browse/HAR-89
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @copyright FA Solutions Oy
 * @version 1.7.0
 * @example
 * const {TabBar, TabPanel} = useTabs("abc", [{ id: "tab0", label: "Tab", content: <div/>}]);
 * <TabBar />
 * <TabPanel />
 */
const useTabs: TabHelperType<UseTabsReturnProps> = (uid, tabs = []) => {
  const activeId = tabIdTrigger.useValue("") || "";
  console.log(activeId);
  const TabPanel = React.useCallback(
    (props: HTMLPropsType<HTMLDivElement>): JSX.Element => {
      const tabPanelContent = tabs.find(({ id }) => id === activeId)?.content;

      return (
        <div
          {...props}
          style={{ width: "100%", height: "100%", ...props.style }}
        >
          {tabPanelContent || (
            <Center
              children={<Text value="No tab selected" color="disabled" />}
            />
          )}
        </div>
      );
    },
    [activeId]
  );

  const TabBar = React.useCallback(
    (): JSX.Element => <TabsBar {...{ tabs, uid }} />,
    []
  );
  return { TabBar, TabPanel, activeTabId: activeId };
};

export { ITabsProps, useTabs };
