import { getIconDefinition } from "@faharmony/icons";
import { IFABarProps, IGlobalBarProps } from "../types";
import useLanguageSwitcher from "./language";
import useUserAction from "./user";
import useThemeAction from "./theme";

const faQuestionCircle = getIconDefinition(
  "M256 340c-15.464 0-28 12.536-28 28s12.536 28 28 28 28-12.536 28-28-12.536-28-28-28zm7.67-24h-16c-6.627 0-12-5.373-12-12v-.381c0-70.343 77.44-63.619 77.44-107.408 0-20.016-17.761-40.211-57.44-40.211-29.144 0-44.265 9.649-59.211 28.692-3.908 4.98-11.054 5.995-16.248 2.376l-13.134-9.15c-5.625-3.919-6.86-11.771-2.645-17.177C185.658 133.514 210.842 116 255.67 116c52.32 0 97.44 29.751 97.44 80.211 0 67.414-77.44 63.849-77.44 107.408V304c0 6.627-5.373 12-12 12zM256 40c118.621 0 216 96.075 216 216 0 119.291-96.61 216-216 216-119.244 0-216-96.562-216-216 0-119.203 96.602-216 216-216m0-32C119.043 8 8 119.083 8 256c0 136.997 111.043 248 248 248s248-111.003 248-248C504 119.083 392.957 8 256 8z"
); //light

const helpDeskOnClick = new Function(
  "",
  `window.open("https://support.fasolutions.com/hc/en-us/", "_blank")`
);

const useStaticActions = (devMode: boolean): IFABarProps["actions"] => {
  return devMode
    ? []
    : [
        {
          id: "helpDesk",
          icon: faQuestionCircle,
          tooltip: "FA Help Desk",
          onClick: helpDeskOnClick as any,
        },
      ];
};

export default ({
  multilingual,
  devMode = false,
  _harmony = false,
  accountUrl,
  logout,
}: IGlobalBarProps & {
  accountUrl: string;
  logout: () => void;
}): IFABarProps["actions"] => {
  devMode = devMode || _harmony;
  const userActions = useUserAction(accountUrl, logout, devMode);
  const themeAction = useThemeAction();
  const staticActions = useStaticActions(devMode);
  const languageSwitchActions = useLanguageSwitcher(multilingual);
  return [
    // Action to manage darkMode/theme
    themeAction,
    // Other actions without logic
    ...staticActions,
    // Add language switcher
    ...languageSwitchActions,
    // Add user action menu
    ...userActions,
  ];
};
