import React from "react";
import { NOOP } from "@faharmony/helpers";
import { StyledToggle } from "./style";

interface IToggleProps {
  /** Component Unique ID */
  id?: string;
  /** Addition CSS classes */
  className?: string;
  /** Name of checkbox. You should alway assign a name  @requires true */
  name?: string;
  /** Value used when you'll post data  @default */
  value?: string;
  /** Default value of Toggle */
  defaultChecked?: boolean;
  /** State of checked  @default false */
  checked?: boolean;
  /** Disable the toggle  @default false */
  disabled?: boolean;
  /** Trigged when the toggle change  @param {React.ChangeEvent} e */
  onToggle?: (e: React.ChangeEvent) => void;
  /** Trigged when the toggle is on the left. */
  onRight?: (e: React.ChangeEvent) => void;
  /** Trigged when the toggle is on the right. */
  onLeft?: (e: React.ChangeEvent) => void;
  /** Label of toggle (on-left-side) */
  label?: string;
}

/**
 * Toggle
 * ---
 * Interactive toggle component
 * @jira https://fasolutions.jira.com/browse/HAR-77
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @copyright FA Solutions Oy
 * @version 1.6.0
 * @example <Toggle checked={true} onToggle={e=>console.log(e.target.checked)} />
 */
const Toggle = React.forwardRef<HTMLInputElement, IToggleProps>(
  (props, ref) => {
    const {
      className,
      name,
      defaultChecked,
      checked,
      disabled = false,
      value = "",
      onToggle = NOOP,
      onRight = NOOP,
      onLeft = NOOP,
      id,
    } = props;

    const checkedProp = React.useMemo(
      () => (checked !== undefined ? { checked } : { defaultChecked }),
      [checked, defaultChecked]
    );

    const cls = ["Toggle", className || ""].join(" ");

    const onChangeHandler = (e: React.ChangeEvent<HTMLInputElement>) => {
      !!onToggle && onToggle(e);
      e.target.checked ? onRight(e) : onLeft(e);
    };

    return (
      <StyledToggle className={cls} id={id}>
        <input
          ref={ref}
          onChange={onChangeHandler}
          type={"checkbox"}
          id={name}
          name={name}
          value={value}
          disabled={disabled}
          {...checkedProp}
        />
        <label htmlFor={name} />
      </StyledToggle>
    );
  }
);

export { Toggle, IToggleProps };
