import React from "react";
import { useWatch, useFormContext, Control } from "./hook-form";

/** Reset form values using the form control.
 * @example const {control} = useForm();
 * <Button value="Reset" onClick={() => resetFormValues(control)} />
 */
export const resetFormValues = <T extends object>(control: Control<T>) => {
  Object.entries(control.defaultValuesRef.current).forEach(([k, v]) =>
    control.setValue(k, v as any)
  );
};

/** Component to render current values of the parent form. */
export const FormValuesWatcher = () => (
  <pre>
    {JSON.stringify(useWatch({ control: useFormContext().control }), null, 2)}
  </pre>
);
