import React from "react";
import { FABar } from "@faharmony/navigation";
import { IGlobalBarProps } from "./types";
import useGlobalActions from "./actions";
import useGlobalApps from "./apps";
import { useValidKeycloak } from "./keycloak";
import { createManifest } from "./webmanifest";

export const GlobalBar = (props: IGlobalBarProps) => {
  const apps = useGlobalApps(props._harmony || props.devMode);
  const actions = useGlobalActions({ ...props, ...useValidKeycloak(props) });
  createManifest(apps);
  return <FABar apps={apps} actions={actions} />;
};
