import React from "react";
import { getTextDimensions } from "@faharmony/theme";
import {
  useResizeObserver,
  useDebounceValue,
  Trigger,
} from "@faharmony/helpers";
import { useQueryParam, insertQueryParam } from "@faharmony/router";
import {
  ITabsProps,
  ITabs,
  TabHelperType,
  UseVisibleTabsProps,
  UseActiveTabIdProps,
} from "./types";

export const tabIdTrigger = new Trigger<string>();

const getAvgLength = (tabs: ITabsProps[]) =>
  tabs.reduce((acc, tab) => acc + getTextDimensions(tab.label).width + 16, 0) /
  tabs.length;

const useActiveTabId: TabHelperType<UseActiveTabIdProps> = (uid, tabs) => {
  const tabParamId = "tab-" + encodeURIComponent(uid.replace(" ", "_"));
  const param = useQueryParam(tabParamId);

  const defaultActiveId =
    param || tabs.find((tab) => tab.defaultActive)?.id || "";
  const [activeId, setActiveId] = React.useState<string>(defaultActiveId);

  React.useEffect(() => {
    tabIdTrigger.publish(activeId);
    tabs.length > 1 && insertQueryParam(tabParamId, activeId);
  });

  return { activeId, setActiveId };
};

export const useVisibleTabs: TabHelperType<UseVisibleTabsProps> = (
  uid,
  tabs
) => {
  // ActiveId
  const { activeId, setActiveId } = useActiveTabId(uid, tabs);
  // State - tabs
  const [state, setState] = React.useState<ITabs>({
    visibleTabs: tabs,
    hiddenTabs: [],
  });

  const { ref, width = 0 } = useResizeObserver<HTMLDivElement>();
  const switcherWidth = useDebounceValue(width, 100);

  React.useEffect(() => {
    const visibleTabCount = Math.floor(width / getAvgLength(tabs));

    const originalVisibleTabs = tabs.slice(0, visibleTabCount),
      originalHiddenTabs = tabs.slice(visibleTabCount);

    // Make active tab visible
    const activeTabHiddenIndex = originalHiddenTabs.findIndex(
      (tab) => tab.id === activeId
    );
    if (activeTabHiddenIndex > -1) {
      const lastVisibleTab = originalVisibleTabs.pop();
      originalVisibleTabs.push(originalHiddenTabs[activeTabHiddenIndex]);
      if (lastVisibleTab) originalHiddenTabs.unshift(lastVisibleTab);
    }

    setState({
      visibleTabs: originalVisibleTabs,
      hiddenTabs: originalHiddenTabs.filter((tab) => tab.id !== activeId),
    });
  }, [activeId, switcherWidth]);

  return { ...state, ref, activeId, setActiveId };
};
