import React from "react";
import { useFormContext, Controller, ControllerProps } from "../hook-form";
import { PickerInput, IPickerProps } from "./PickerInput";

interface IDatePickerProps extends IPickerProps {
  name?: string;
  defaultValue?: Date;
  rules?: ControllerProps<any>["rules"];
  render?: ControllerProps<any>["render"];
  onFocus?: any;
}

/**
 * DatePicker
 * ---
 * Description of component
 * @jira https://fasolutions.jira.com/browse/HAR-74
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @copyright FA Solutions Oy
 * @version 1.6.0
 * @example <DatePicker />
 */
const DatePicker = (props: IDatePickerProps): JSX.Element => {
  const { name, rules, render, onFocus, defaultValue, ...rest } = props;
  // Check if component is placed in a react-hook-form.
  try {
    const { control, setValue } = useFormContext();
    return (
      <Controller
        as={
          (
            <PickerInput
              {...rest}
              onDayChange={(date) => setValue("date", date)}
            />
          ) as any
        }
        control={control}
        name={name || "date"}
        defaultValue={defaultValue}
        rules={rules}
        render={render}
        onFocus={onFocus}
      />
    );
  } catch {
    return <PickerInput {...rest} />;
  }
};

export { DatePicker, IDatePickerProps };
