import React from "react";
import { styled, theme, CSSProperties } from "@faharmony/theme";
import { IconDefinition } from "@faharmony/icons";
import { Icon } from "../Icon";

interface ICardProps {
  id?: string;
  style?: CSSProperties;
  className?: string;
  onClick?: (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => void;

  heading: string;
  body?: string;
  icon?: IconDefinition;
  iconColor?: string;
}

const StyledCard = styled.div`
  box-shadow: ${theme.shadow.default};
  border-radius: ${theme.radius.sm};
  width: 100%;
  height: auto;
  background-color: ${theme.colorBackground.elevated};
  padding: ${theme.spacing.md};
  display: grid;
  grid-template-columns: max-content auto;
  gap: ${theme.spacing.md};
  align-items: flex-start;

  .Card-text {
    .Card-heading {
      display: block;
      color: ${theme.colorText.primary};
    }
    .Card-body {
      color: ${theme.colorText.secondary};
    }
  }
`;

/**
 * Card
 * ---
 * Component to display information as a bound card.
 * @jira https://fasolutions.jira.com/browse/HAR-102
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @copyright FA Solutions Oy
 * @version 1.7.0
 * @example <Card />
 */
const Card = React.forwardRef<any, ICardProps>(
  (props, ref): JSX.Element => {
    const { heading, icon, body, iconColor, ...rest } = props;
    return (
      <StyledCard ref={ref} {...rest}>
        <div className="Card-icon">
          {icon && <Icon icon={icon} color={iconColor} />}
        </div>
        <div className="Card-text">
          <p className="Card-heading">{heading}</p>
          {body && <small className="Card-body">{body}</small>}
        </div>
      </StyledCard>
    );
  }
);

export { Card, ICardProps };
