import React from "react";

import { Text, Divider, Toolbar } from "@faharmony/components";
import { Breadcrumbs, IBreadcrumb } from "@faharmony/navigation";
import { IPageViewProps } from "./types";
import { StyledHeader, StyledTitle } from "./style";

const getCrumbs = (caption: IPageViewProps["caption"]) => {
  const crumbs: IBreadcrumb[] = caption
    ? typeof caption === "string"
      ? [
          {
            id: "default",
            label: caption,
          },
        ]
      : caption
    : [];
  return crumbs;
};

const Title = (props: IPageViewProps): JSX.Element => {
  const { caption, heading, actions } = props;
  const [crumbs] = React.useState(getCrumbs(caption));
  return (
    <StyledTitle isCaption={crumbs.length > 0}>
      <div className="PageView-header-title">
        {crumbs.length > 0 && <Breadcrumbs crumbs={crumbs} variant="strong" />}
        <Text value={heading} variant="h2" truncate />
      </div>
      <div className={"PageView-header-actions"}>{actions}</div>
    </StyledTitle>
  );
};

export const Header = (
  props: IPageViewProps & { TabBar: () => JSX.Element }
) => {
  const { toolbarContent, tabs, TabBar } = props;
  return (
    <StyledHeader className="PageView-header">
      <Title {...props} />
      {toolbarContent && <Toolbar children={toolbarContent} />}
      {tabs ? <TabBar /> : <Divider />}
    </StyledHeader>
  );
};
