import React from "react";
import { getTextDimensions } from "@faharmony/theme";
import { faCaretDown } from "@faharmony/icons";
import { Text, ButtonPopover, Tooltip } from "@faharmony/components";

import { maxWidth, StyledMoreAction, StyledTabBar, StyledTab } from "./style";
import { ITabsProps, ITabsBarProps } from "./types";
import { useVisibleTabs } from "./helpers";

const Tab = ({
  tab: { id, label, disabled = false },
  active,
  handleClick,
}: {
  tab: ITabsProps;
  active: boolean;
  handleClick: (id: string) => void;
}): JSX.Element => {
  const isTextOverflow = getTextDimensions(label).width > maxWidth - 16;
  const tab = (
    <StyledTab
      key={id}
      active={active}
      onClick={() => (disabled ? {} : handleClick(id))}
      disabled={disabled}
    >
      <Text value={label} truncate />
    </StyledTab>
  );
  return isTextOverflow ? <Tooltip value={label}>{tab}</Tooltip> : tab;
};

const TabsBar = ({ tabs, uid }: ITabsBarProps): JSX.Element => {
  const {
    visibleTabs,
    hiddenTabs,
    ref,
    activeId,
    setActiveId,
  } = useVisibleTabs(uid, tabs);
  const menuItems = React.useMemo(
    () =>
      hiddenTabs.map(({ id, label, disabled }) => ({
        id,
        label,
        disabled,
        onClick: () => setActiveId(id),
      })),
    [hiddenTabs]
  );
  return (
    <StyledTabBar ref={ref}>
      {hiddenTabs.length > 0 && (
        <StyledMoreAction>
          <ButtonPopover
            icon={faCaretDown}
            spacing="xs"
            popoverMenuItems={menuItems}
          />
        </StyledMoreAction>
      )}
      {visibleTabs.map((tab) => (
        <Tab
          key={tab.id}
          tab={tab}
          handleClick={setActiveId}
          active={tab.id === activeId}
        />
      ))}
    </StyledTabBar>
  );
};

export { TabsBar, ITabsProps, ITabsBarProps };
