import React from "react";
import { Icon } from "@faharmony/components";
// import {
//   //   faTimesCircle,
//   faExclamationCircle,
//   faCheckCircle,
//   faCircleNotch,
// } from "@faharmony/icons";
// import { theme } from "@faharmony/theme";

import { IInputFieldProps, _IInputFieldContainerProps } from "./types";
import { StyledInputField, commonClass } from "./style";

/**
 * InputField
 * ---
 * Base Input component.
 * @jira https://fasolutions.jira.com/browse/HAR-ticker
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @copyright FA Solutions Oy
 * @version 1.6.0
 * @example <InputField />
 */
const InputField = React.forwardRef<HTMLInputElement, IInputFieldProps>(
  (props, ref) => {
    let {
      isClearable = true,
      disabled = false,
      readOnly = false,
      spacing = "md",
      className = commonClass,
      onInputClear,
      placeholder = "Enter value...",
      prefixIcon,
      prefixText,
      suffixIcon,
      suffixText,
      error = false,
      success = false,
      autoComplete = "off",
      textAlign = "left",
      inputComponent,
      ...inputProps
    } = props;
    // Set values while loading
    // if (isLoading) {
    //   error = false;
    //   success = false;
    //   readOnly = true;
    // }

    const inputField = inputComponent || (
      <input
        {...inputProps}
        className={className + " input"}
        disabled={disabled}
        ref={ref}
        readOnly={readOnly}
        placeholder={placeholder}
        autoComplete={autoComplete}
        style={{ textAlign }}
      />
    );
    // Field prefix
    const prefixComponent = (prefixIcon || prefixText) && (
      <div className={className + " prefix"}>
        {prefixIcon ? <Icon icon={prefixIcon} /> : prefixText || ""}
      </div>
    );

    // Field suffix
    const suffixComponent = (suffixIcon || suffixText) && (
      <div className={className + " suffix"}>
        {suffixIcon ? <Icon icon={suffixIcon} /> : suffixText || ""}
      </div>
    );

    const showClearButton = isClearable && !disabled && !readOnly;
    //   isClearVisible;

    return (
      <StyledInputField
        className={className + " inputContainer"}
        isClearable={showClearButton}
        disabled={disabled}
        spacing={spacing}
        readonly={readOnly}
        prefixIcon={prefixIcon}
        prefixText={prefixText}
        suffixIcon={suffixIcon}
        suffixText={suffixText}
        textAlign={textAlign}
        error={error}
        success={success}
      >
        {inputField}
        {prefixComponent}
        {suffixComponent}
      </StyledInputField>
    );
  }
);

export { InputField, IInputFieldProps };
