import React, { useCallback, useRef, useEffect, useMemo } from "react";
import { AnimatePresence } from "@faharmony/theme";
import { faBellSlash } from "@faharmony/icons";
import { useArray } from "@faharmony/helpers";
import { Button } from "@faharmony/components";
import { IToastProps } from "./types";
import { Container } from "./style";
import Toast from "./Toast";
import {
  addToastTrigger,
  removeToastTrigger,
  clearToastTrigger,
} from "./triggers";

/**
 * ToastContainer
 * ---
 * Wrap the app with this to show toast notifications
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 * @version 1.5.0
 * @example
 * <ToastContainer />
 */
const ToastContainer = (): JSX.Element => {
  const toasts = useArray<IToastProps>([]);
  const containerRef = useRef<HTMLDivElement>(null);

  /** Add a new toast to the toast-notification stack */
  const addToastCallback = useCallback((toast: IToastProps) => {
    toasts.removeById(toast.id);
    toasts.push(toast);
  }, []);
  addToastTrigger.useSubscribe(addToastCallback);

  /** Remove a specific toast using toast-Id from the toast-notification stack. */
  const removeToastCallback = useCallback((id: string) => {
    toasts.removeById(id);
  }, []);
  removeToastTrigger.useSubscribe(removeToastCallback);

  /** Clear the toast-notification stack */
  const clearAllToasts = useCallback(() => {
    toasts.clear();
  }, []);
  clearToastTrigger.useSubscribe(clearAllToasts);

  useEffect(() => {
    if (containerRef.current) {
      containerRef.current.scrollTo({
        top: containerRef.current.scrollHeight,
      });
    }
  }, [containerRef, toasts.value]);

  const toastCount = useMemo(() => toasts.value.length, [toasts.value]);

  return (
    <Container className={"toast-container"} ref={containerRef}>
      <AnimatePresence>
        {toasts.value.map((toast) => (
          <Toast key={toast.id} {...toast} />
        ))}
      </AnimatePresence>
      {toastCount > 5 && (
        <div id="clearAllToasts">
          <Button
            onClick={clearAllToasts}
            icon={faBellSlash}
            tooltip="Clear all notifications"
            variant="secondary"
            spacing="sm"
          />
        </div>
      )}
    </Container>
  );
};

export { IToastProps, ToastContainer };
export { addToast, removeToast, clearToasts } from "./triggers";
